body {
  margin: 0;
  padding: 0;
  background-color: rgb(78, 78, 78);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title{
    font-family: 'Alfa Slab One', cursive;
    font-weight: 400;
    color: rgb(219, 219, 219); 
    text-align: center;
}

.credits{
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    color: rgb(219, 219, 219); 
    text-align: center;
}
.pad{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 11vw;
    height: 11vw;
    background-color: rgb(160, 160, 160);
    border-radius: 5%;
    margin: 10px 10px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    



}
.letter{
    font-family: 'Alfa Slab One', cursive;
    color: rgb(37, 37, 255);
    font-size: 3vw;
    margin: 0 0 2% 0;
}
.instrument-name{
    font-family: 'Open Sans', sans-serif;
    color: rgb(78, 78, 78);
    font-weight: 700;
    font-size: 1.5vw;
}



.play{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 11vw;
    height: 11vw;
    background-color: rgb(160, 160, 160);
    border-radius: 5%;
    margin: 10px 10px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    -webkit-animation-name: play;
            animation-name: play;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-fill-mode:backwards;
            animation-fill-mode:backwards;
}

@-webkit-keyframes play{
    0%{
        background-color: rgb(160, 160, 160);
    }
    50%{
        background-color: rgb(238, 205, 15);
    }
}

@keyframes play{
    0%{
        background-color: rgb(160, 160, 160);
    }
    50%{
        background-color: rgb(238, 205, 15);
    }
}

@media (max-width: 720px){
    .pad{
        width: 20vw;
        height: 20vw;
    }
    .letter{
        display: none;
    }
    .instrument-name{
        font-size: 3vw;
        color: rgb(37, 37, 255);
    }
    .play{
        width: 20vw;
        height: 20vw;
    }
  }
.pads-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 44vw;
    margin-left: 28vw;
  }
  
  @media (max-width: 720px){
    .pads-container{
      width: 80vw;
      margin-left: 10vw;
    }
  }
